* {
  font-family: proxima-soft, sans-serif;
  font-style: normal;
  font-weight: 200;
  font-size: 18px;
  letter-spacing: 2.75px;
  word-spacing: 2px;
  /* background-color: #eceff1;*/
}

html,
body {
  margin: 0px;
}

h1 {
  font-family: nsyn-n1, adobe-notdef;
  font-style: normal;
  font-weight: 100;
  font-size: 45px;
}

.images {
  width: auto;
  height: auto;
}

.picture {
  width: 100%;
  height: 500px;
  opacity: 0.9;
}

.box {
  display: -ms-flexbox;
  display: flex;
  /*flex-direction: column;*
  /* align-items: flex-end;
  justify-content: flex-end;*/
}

.slogan {
  position: absolute;
  top: 155px;
  width: auto;
  left: 38px;
  /* border-top: 1px solid white;
  border-bottom: 1px solid white;*/
  padding: 0px 18px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: rgba(0, 0, 0, 0.4);
}

.palabras {
  position: absolute;
  top: 410px;
  right: 38px;
  padding: 0px 38px;
  background-color: rgba(0, 0, 0, 0.4);
  height: auto;
  width: auto;
  letter-spacing: 1.5px;
  word-spacing: 2px;
}

.sentence {
  color: white;
}

.boite {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width:100%;
  overflow-x: auto;
}

.tags {
  margin: 0px 10px;
}

.border {
  border: 3px solid green;
  padding: 0px 50px;
  margin-bottom: 76px;
  background-color: #f9f9f9;
}

.biographie {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 76px 0px;
}

.pictureArtist {
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  -o-transition: transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  margin: 18px 0px;
  -webkit-box-shadow: 0px 2px 3px 5px #999999;
          box-shadow: 0px 2px 3px 5px #999999;
  width: 174px;
  height: 174px;
}

.pictureArtist:hover {
  -webkit-transform: scale(1.3);
      -ms-transform: scale(1.3);
          transform: scale(1.3);
  -webkit-box-shadow: 0px 0px 0px 0px;
          box-shadow: 0px 0px 0px 0px;
}

.contenu {
  /*border: 3px dashed greenyellow;*/
  padding: 28px 38px;
  height: 450px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  /*border: 1px solid green;*/
  height: 145px;
  background-image: url("../images/fondAbout.png");
  color: white;
  opacity: 0.85;
}

.subtitle {
  font-size: 26px;
}

.sectionBack {
  background-color: #f9f9f9;
}

.footer {
  height: 100px;
  width: auto;
  background-image: url("../images/fondAbout.png");
  color: white;
  opacity: 0.85;
  padding: 38px;
}

.test {
  border: 1px solid #999999;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: auto;
}

.choix {
  padding: 38px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 150px;
}

select {
  border: 1px solid #999999;
  border-radius: 5px;
  width: auto;
  padding: 2px 10px;
  margin: 0px 10px;
}

option {
  letter-spacing: 2px;
  font-style: italic;
  letter-spacing: 2px;
}

.albums p,
.albums a {
  /* border: 3px dashed green; */
  width: 174px;
  height: auto;
  padding: 0px 2px;
  margin: 18px 0px;
}

.albums a:hover {
  cursor: pointer;
  font-style: italic;
}

.albums {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 350px;
  -ms-flex-align: center;
      align-items: center;
}

.teste {
  border: 1px solid rebeccapurple;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.boites {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
}

.boites p {
  width: 174px;
  height: auto;
}



